import {ElementsTree} from "../../../model/ElementsTree";
import {Node} from "../../../model/Graph";
import {Schema} from "../schema/schema";
import {ValueObjectMetadata} from "./get-vo-metadata";
import {isEntityCollection} from "./is-entity-collection";
import {isStateVo} from "./is-state-vo";
import {isVoStoredInDb} from "./is-vo-stored-in-db";

export const isProjectionCandidate = (vo: Node, voMeta: ValueObjectMetadata, elementsTree: ElementsTree): boolean => {
  return isVoStoredInDb(voMeta) && !isStateVo(vo) && !isEntityCollection(voMeta.schema || new Schema({}), elementsTree);
}
